.userName{
  margin-left: 20px;
}

.chat-container{
  height: 80vh;
  overflow-y: scroll;
  border: 1px solid black;
}

.chatbox{
  border: 2px solid grey;
  border-radius: 20px;
  margin: 5px 0;
  padding: 10px;
  background-color: bisque;
  margin-left: 20px;
}

.me .chatbox{
  background-color: cadetblue;
  margin-right: 20px;
}

.container{
  display: flex;
}

.container.me{
  flex-direction: row-reverse;
}

.btm{
  position: fixed;
  width: 100%;
  bottom: 20px;
  display: flex;
  flex-direction: row;
}

.btm input{
  padding: 10px 0;
  margin-left: 10px;
  margin-right: 10px;
  /* flex-grow: 1; */
  width: 1000px;
  background-color: beige;
  border-radius: 10px;
  border: black solid 1px;
}

.btm button{
  padding: 10px 0;
  margin-left: 10px;
  margin-right: 10px;
  width: 80px;
  border-radius: 10px;
  cursor: pointer;
}

.login{
  margin: 0;
  position: absolute;
  top: 50%;
  left: 45%;
}

.login button{
  width: 100px;
  height: 30px;
  border-radius: 10px;
  background-color: bisque;
  cursor: pointer;
}